export default {
	data() {
		return {
			nameValue: '',
			positionValue: '',
			phoneValue: 0,
			nickname: '',
			avatarUrl: '',
			firmName: '',
			position: '',
			phone: 0,
			u_id: 0,
			ImgUrl: this.ImgUrl
		}
	},
	methods: {
		nameChange(e) {
			this.nameValue = e.target.value
		},
		
		positionChange(e) {
			this.positionValue = e.target.value
		},
		
		phoneChange(e) {
			this.phoneValue = e.target.value
		},
		
		// 获取用户信息
		getUser(id) {
			let baseUrl = this.WebUrl
			let that = this
			uni.request({
				url: baseUrl + '/getUser',
				data: {
					u_id: id
				},
				success: (res) => {
					console.log(res)
					that.nickname = res.data.data.u_name || ''
					that.avatarUrl = res.data.data.head || ''
					that.position = res.data.data.position || ''
					that.phone = res.data.data.phone || 12345678910
					
					// 获取公司名字
					uni.request({
						url: baseUrl + '/getUser',
						data: {
							u_id: res.data.data.u_id
						},
						success: (resd) => {
							console.log(resd)
							that.firmName = resd.data.data.com_name || ''
						}
					})
				}
			})
		},
		
		updateUser() {
			let baseUrl = this.WebUrl
			let that = this
			let name = this.nameValue === '' ? this.nickname : this.nameValue
			let position = this.positionValue === '' ? this.position : this.positionValue
			let phone = this.phoneValue === 0 ? this.phone : this.phoneValue
			console.log(position)
			uni.request({
				url: baseUrl + '/updateUser',
				method: 'POST',
				data: {
					u_id: that.u_id,
					u_name: name,
					position: position,
					head: that.avatarUrl,
					phone: phone
				},
				success: (res) => {
					console.log(res)
					uni.showModal({
						title: "提示",
						content: res.data.msg,
						showCancel: false,
						success: (resd) => {
							if (resd.confirm) {
								uni.navigateBack({
									delta: 1
								})
							}
						}
					})
				}
			})
		}
	},
	onLoad: function(option) {
		this.u_id = option.u_id
		
		this.getUser(option.u_id)
	}
}
